.iq-price-box {
    &.iq-price-box-standard {
        .price-heading {
            .iq-price-title {
                letter-spacing: 0.063rem;
            }
        }
    }

    &.iq-price-box-tab {
        .iq-price-header {
            padding: 40px;
            border-radius: 15px 15px 0 0;
        }

        .iq-price-body {
            padding: 40px;
            border-radius: 0 0 15px 15px;
        }

        .iq-price-footer {
            padding: 0 40px 40px 60px;
            border-radius: 0 0 15px 15px;
        }
    }
}

.iq-pricetable-tabs {
    .nav {
        width: 160px;
        color: var(--bs-body-color);
        padding: 0.25rem;

        .nav-link{
            color: var(--bs-body-color);

            &:hover,&:focus{
                color: var(--bs-primary);
            }

            &.active{
                color: $white;
            }
        }
    }

    .nav-tunnel {
        .nav-link {
            padding: 0.25rem 1rem;
            border-radius: 50px;
        }
    }
}

@media (max-width: 767px){
    .iq-price-box {
        &.iq-price-box-standard {
            .price-heading {
                .iq-price-title {
                    font-size: 14px;
                }
            }
        }
    }
}